body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-container {
    margin-bottom: 2em;
    text-align: center;
}

@mixin setLinkSelectorStyle {
    &:hover,
    &:link,
    &:active,
    &:visited {
        @content;
    }
}

.nav-link {
    padding: 1.5em;
    color: #605e5e;
    @include setLinkSelectorStyle {
        color: #797979;
    }
}

.portfolio-container {
    height: 200px;
    text-align: center;
}

.about-body {
  color: #605e5e;
}

.about-paragraph {
  font-family: Lato;
  font-size: 1em;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  line-height: 1.4em;
  color: #605e5e;
}

@mixin setLinkSelectorStyle {
  &:hover,
  &:link,
  &:active,
  &:visited {
    @content;
  }
}

.about-link {
  color: #605e5e;
  text-decoration: underline;
  @include setLinkSelectorStyle {
    color: #797979;
  }
}

.footer-container {
    margin: 4em;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    text-align: center;
}

.footer-paragraph {
    font-family: Lato;
    font-size: 1.2em;
    font-style: normal;
    font-weight: normal;
    -webkit-font-feature-settings: normal;
            font-feature-settings: normal;
    font-variant: normal;
    line-height: 0.8em;
    color: #605e5e;
}

@mixin setLinkSelectorStyle {
    &:hover,
    &:link,
    &:active,
    &:visited {
        @content;
    }
}

.footer-link {
    color: #605e5e;
    @include setLinkSelectorStyle {
        color: #797979;
    }
}

.header-container {
    margin-top: 1em;
}

.header-logo {
    width: 18em;
    padding-bottom: 2em;
}

.header-social-ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
}

.header-social-li {
    display: inline-block;
    width: 2em;
    padding-left: 1em;
}

.body-container {
  background-color: rgb(250, 250, 250);
}

.app-parent-container {
  display: -webkit-flex;
  display: flex;
  height: 100%;
  min-height: 100%;
  margin: 0;
  color: #605e5e;
}

.app-child-container {
  width: 50%;
  margin: auto;
}

